import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment'

@Pipe({
  name: 'dateFormatter'
})
export class DateFormatterPipe implements PipeTransform {
  

  transform(date: any, args?: any): any {
    let d = new Date(date);
    return moment(d).format('DD/MM/YYYY');

  }
}



